<template>
  <div>
    <div>
      <el-container>
        <el-header>
          <router-link to="/">
            <i class="el-icon-s-platform"></i>
            <span></span>
          </router-link>
          <span class="adminName"
            >:{{ adminName }}
            <el-button size="mini" round type="primary" @click="exit"
              ></el-button
            ></span
          >
        </el-header>
      </el-container>
      <el-container>
        <el-header>
          <el-radio-group v-model="isCollapse" style="margin-bottom: 20px">
            <el-radio-button :label="false"></el-radio-button>
            <el-radio-button :label="true"></el-radio-button>
          </el-radio-group></el-header
        >
      </el-container>
      <el-container
        style="border: 1px solid #eee"
        :style="{ height: h - 135.5 + 'px' }"
      >
        <el-menu
          router
          default-active="garbageClass"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          :collapse="isCollapse"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#25c89b"
        >
          <el-menu-item index="afterindex">
            <i class="el-icon-s-home"></i>
            <span slot="title" class="menu-yiji"></span>
          </el-menu-item>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-s-help"></i>
              <span slot="title" class="menu-yiji"></span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="garbageClass">
                <i class="el-icon-s-data"></i>
                <span></span>
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title">
              <i class="el-icon-s-platform"></i>
              <span slot="title" class="menu-yiji"></span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="user">
                <i class="el-icon-s-custom"></i>
                <span></span>
              </el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
              <el-menu-item index="collectorUsers">
                <i class="el-icon-user-solid"></i>
                <span></span>
              </el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
              <el-menu-item index="feedbacks">
                <i class="el-icon-s-shop"></i>
                <span>/</span>
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-menu-item index="4">
            <i class="el-icon-s-promotion"></i>
            <span slot="title" class="menu-yiji"></span>
          </el-menu-item>
        </el-menu>

        <el-container>
          <el-main style="width: 100%">
            <div style="margin-bottom: 20px">
              <!--  -->
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/' }"
                  ></el-breadcrumb-item
                >
                <el-breadcrumb-item>/</el-breadcrumb-item>
              </el-breadcrumb>
              <el-button
                type="info"
                round
                icon="el-icon-circle-close"
                @click="toggleSelection()"
                ></el-button
              >
              <el-button
                type="success"
                round
                icon="el-icon-circle-plus-outline"
                @click="addGarbage = true"
                ></el-button
              >
              <el-button
                type="danger"
                round
                icon="el-icon-delete"
                style="margin-right: 15p + x"
                @click="deleteLists"
                ></el-button
              >
              <div
                style="
                  margin-top: 15px;
                  width: 250px;
                  display: inline-block;
                  margin-left: 10px;
                "
              >
                <el-input
                  placeholder=""
                  v-model="search"
                  clearable
                  autofocus
                  @input="searchInput"
                  class="input-with-select"
                >
                  <el-button
                    slot="append"
                    @click="searchInput"
                    icon="el-icon-search"
                  ></el-button>
                </el-input>
              </div>
            </div>
            <el-table
              ref="multipleTable"
              :data="tableData"
              stripe
              style="width: 100%"
              height="500"
              border
              fit
              @selection-change="handleSelectionChange"
              @row-click="huoquid"
            >
              <el-table-column
                type="selection"
                prop="garbage_id"
              ></el-table-column>
              <el-table-column prop="garbage_name" label="" align="center">
              </el-table-column>
              <el-table-column
                prop="garbage_class_name"
                label=""
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="garbage_remark"
                label=""
                align="center"
              >
              </el-table-column>
              <el-table-column prop="caozuo" label="" align="center">
                <el-button
                  type="warning"
                  icon="el-icon-s-tools"
                  round
                  @click="updateGarbage = true"
                  ></el-button
                >
              </el-table-column>
            </el-table>
          </el-main>
        </el-container>
      </el-container>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addGarbage"
      center
      :before-close="addinfo"
    >
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.garbageName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.garbageClassId" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="()" value="2"></el-option>
            <el-option label="()" value="3"></el-option>
            <el-option label="" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.garbageRemark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addinfo"> </el-button>
        <el-button type="primary" @click="insertGarbage"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="updateGarbage"
      center
      :before-close="updateinfo"
    >
      <el-form :model="garbage">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="garbage.garbage_name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select
            v-model="garbage.garbage_class_name"
            placeholder=""
          >
            <el-option label="" value="1"></el-option>
            <el-option label="()" value="2"></el-option>
            <el-option label="()" value="3"></el-option>
            <el-option label="" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="garbage.garbage_remark"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateinfo"> </el-button>
        <el-button type="primary" @click="updateGarbageShuju"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      adminName: sessionStorage.getItem("name"),
      select: "",
      search: "",
      indexOf: "",
      addGarbage: false,
      updateGarbage: false,
      h: "",
      isCollapse: true,
      tableData: [],
      multipleSelection: [],
      form: {
        garbageName: "",
        garbageClassId: "",
        garbageRemark: "",
      },
      formLabelWidth: "50px", //
      garbageIds: "",
      garbage: {
        garbage_name: "",
        garbage_class_name: "",
        garbage_remark: "",
      },
    }
  },
  methods: {
    // garbage_id
    handleSelectionChange(val) {
      // console.log('val:',val);
      this.multipleSelection = val
      // console.log('mul:',this.multipleSelection);
      let ids = []
      this.multipleSelection.map((key) => {
        ids.push(key.garbage_id)
      })
      this.garbageIds = ids
      console.log("", this.garbageIds)
    },
    huoquid(val) {
      this.garbage = val
      console.log(this.garbage)
    },
    // 
    windowHeight() {
      let de = document.documentElement
      let h =
        self.innerHeight ||
        (de && de.offsetHeight) ||
        document.body.offsetHeight
      this.h = h
      console.log(this.h)
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
      console.log("11")
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$message({ message: "", type: "info" })
        this.$refs.multipleTable.clearSelection()
      }
    },
    //
    addinfo() {
      this.addGarbage = false
      this.$message({ message: "", type: "info" })
    },
    //
    updateinfo() {
      this.updateGarbage = false
      this.$message({ message: "", type: "info" })
    },
    // 
    getLists() {
      let url = `/getGarbages`
      this.axios.get(url).then((res) => {
        console.log(res)
        this.tableData = res.data.results
      })
    },
    // 
    insertGarbage() {
      let url = `/addGarbage/${this.form.garbageName}/${this.form.garbageClassId}/${this.form.garbageRemark}`
      console.log(url)
      this.axios.get(url).then((res) => {
        console.log(res)
        if (res.data.code == 200) {
          this.addGarbage = false
          this.$message({
            message: "",
            type: "success",
          })
          this.form = {}
          this.getLists()
        } else {
          this.$message({
            message: "",
            type: "warning",
          })
          this.form = {}
        }
      })
    },
    // 
    updateGarbageShuju() {
      let url = `/updateGarbage/${this.garbage.garbage_id}/${this.garbage.garbage_name}/${this.garbage.garbage_class_name}/${this.garbage.garbage_remark}`
      console.log(url)
      this.axios.get(url).then((res) => {
        console.log(res)
        if (res.data.code == 200) {
          this.addGarbage = false
          this.$message({
            message: "",
            type: "success",
          })
          this.updateGarbage = false
          this.getLists()
        } else {
          this.$message({
            message: "",
            type: "warning",
          })
        }
      })
    },
    // 
    deleteLists() {
      console.log(this.garbageIds)
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "error",
        center: true,
      })
        .then(() => {
          let url = `/deleteGarbages`
          let garbageIds = this.garbageIds.toString()
          console.log("Gids", garbageIds)
          garbageIds = `garbageIds=${garbageIds}`
          this.axios.post(url, garbageIds).then((res) => {
            console.log(res)
            if (res.data.code != 200) {
              this.$message({
                type: "error",
                message: "!",
              })
            } else {
              this.getLists()
              this.$message({
                type: "success",
                message: "!",
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          })
        })
    },
    // 
    deleteGarbage() {
      console.log(this.garbageIds)
      this.$confirm(" ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "error",
        center: true,
      })
        .then(() => {
          let url = `/deleteGarbage`
          let garbageIds = this.garbageIds.toString()
          console.log("Gids", garbageIds)
          garbageIds = `garbageIds=${garbageIds}`
          this.axios.post(url, garbageIds).then((res) => {
            console.log(res)
            if (res.data.code != 200) {
              this.$message({
                type: "error",
                message: "!",
              })
            } else {
              this.getLists()
              this.$message({
                type: "success",
                message: "!",
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          })
        })
    },
    // 
    searchInput() {
      if (this.search) {
        let url = `/searchGarbage/${this.search}`
        this.axios.get(url).then((res) => {
          console.log(res)
          this.tableData = res.data.results
        })
      } else {
        this.getLists()
      }
    },
    exit() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: ",",
          })
          // session
          window.sessionStorage.clear()
          // 
          this.$router.push("/adminLogin")
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          })
        })
    },
  },
  mounted() {
    this.windowHeight()
    this.getLists()
  },
}
</script>

<style lang="scss" scoped>
a {
  text-decoration: none;
  color: #25c89b;
}
body {
  background-color: #545c64;
}
.el-header {
  background-color: #545c64;
  line-height: 60px;
  color: #25c89b;
  i {
    font-size: 28px;
  }
  span {
    font-size: 22px;
    font-family: "";
    margin-left: 10px;
  }
}
::v-deep .el-menu-item-group__title {
  padding: 0;
}
.menu-yiji {
  margin-right: 75px;
}
.el-main {
  background-color: #fff;
}
::v-deep .el-dialog {
  width: 400px !important;
}
::v-deep .el-input-group__append {
  border-radius: 0 20px 20px 0;
}
.adminName {
  position: absolute;
  right: 12px;
  color: #fff;
  font-size: 12px;
}
</style>
